<template>
    <v-layout column align-center justify-center class="Rectangle">
        <v-spacer></v-spacer>
        <v-flex xs1 class="Upload-floor-plans">
            <p>Upload floor plans</p>
        </v-flex>
        <v-flex xs2 class="Create-the-setup-for">
            <p>Create the setup for building.</p>
        </v-flex>
        <v-flex xs5>
            <v-btn v-on:click="$refs.file.click()" fab dark class="Oval" color="#db4326">
                <v-icon dark>add</v-icon>
            </v-btn>
            <input
                type="file"
                ref="file"
                style="display: none"
                v-on:change="previewImage"
                accept="image/*"
            />
        </v-flex>
    </v-layout>
</template>

<script>
import router from '@/router'

export default {
    name: 'UploadFirstPlan',
    data() {
        return {
            selectedFile: ''
        }
    },
    methods: {
        previewImage: function(event) {
            var input = event.target
            if (input.files && input.files[0]) {
                var reader = new FileReader()
                reader.onload = e => {
                    this.selectedFile = e.target.result
                    router.push({ name: 'add-floor-plan', params: { selectedFile: this.selectedFile } })
                }
                reader.readAsDataURL(input.files[0])
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import '@/styles/global.scss';

.Rectangle {
    margin-top: 0;
    height: 1024px;
    border: solid 1px #e0e0e1;
    background-image: url('../../../svg-icons/upload.svg');
    background-size: 30%;
    background-position: center;
}
.Upload-floor-plans {
    width: 205px;
    height: 29px;
    font-family: Roboto;
    font-size: 25px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #2b363b;
}
.Create-the-setup-for {
    width: 205px;
    height: 19px;
    font-family: Roboto;
    font-size: 16px;
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #2b363b;
}
.Oval {
    width: 73px;
    height: 73px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.21);
    background-color: #db4326;
}
</style>

