<template>
    <v-layout column align-center>
        <v-layout align-center justify-center column>
            <v-container v-if="!!selectedFile && selectedFile.length > 0">
                <img class="preview" :src="selectedFile" alt="floor" />
                <v-btn icon class="close-btn" v-on:change="deleteImage">
                    <svgicon icon="close" class="icon"></svgicon>
                </v-btn>
            </v-container>
        </v-layout>
        <v-form ref="form" lazy-validation>
            <v-container>
                <v-badge color="#95c466">
                    <span slot="badge">
                        <v-icon dark small>check</v-icon>
                    </span>
                    <h1 class="Upload-complete">Upload complete</h1>
                </v-badge>
                <h2 class="Add-info">Please add floor plan info</h2>
            </v-container>
            <v-select
                v-model="selectedCity"
                :items="listOfCities"
                item-text="name"
                item-value="locationId"
                v-on:change="selectedCityMethod"
                :rules="[v => !!v || 'City is required']"
                label="Select City"
                required
                class="empty"
            ></v-select>
            <v-select
                v-model="selectedBuilding"
                :disabled="!this.selectedCity"
                :items="listOfBuildings"
                item-text="name"
                item-value="buildingId"
                v-on:change="selectedBuildinglMethod"
                :rules="[v => !!v || 'Building is required']"
                label="Select Building"
                required
                class="empty"
            ></v-select>
            <v-select
                v-model="selectedFloor"
                :disabled="!this.selectedBuilding"
                :items="listOfFloors"
                item-text="name"
                item-value="floorId"
                :rules="[v => !!v || 'Floor is required']"
                label="Select Floor"
                required
                class="empty"
            ></v-select>
            <v-flex right="true">
                <v-btn flat color="#48545b">Cancel</v-btn>
                <v-btn
                    :disabled="!this.selectedFloor"
                    color="#db4326"
                    class="white--text"
                    v-on:click="addLayout"
                >Save</v-btn>
            </v-flex>
        </v-form>
    </v-layout>
</template>

<script>
import '@/compiled-icons/close'
import { mapState, mapActions } from 'vuex'
import router from '@/router'

export default {
    name: 'UploadFirstPlan',
    props: {
        selectedFile: {
            type: String,
            required: true
        }
    },
    data: () => ({
        selectedCity: '',
        selectedBuilding: '',
        selectedFloor: ''
    }),
    computed: {
        ...mapState('buildingInformation', ['listOfCities', 'listOfBuildings', 'listOfFloors']),
        ...mapState('account', ['userLocation'])
    },
    methods: {
        ...mapActions('buildingInformation', ['getCities', 'getBuildings', 'getFloors']),
        ...mapActions('floorLayout', ['uploadLayout']),
        selectedCityMethod() {
            this.selectedBuilding = ''
            this.selectedFloor = ''
            this.getBuildings(this.selectedCity)
        },
        selectedBuildinglMethod() {
            this.selectedFloor = ''
            this.getFloors(this.selectedBuilding)
        },
        addLayout() {
            let imageInformation = { floorId: this.selectedFloor, layoutImage: this.selectedFile }
            this.uploadLayout({ imageInformation, router })
        },
        deleteImage() {
            this.selectedFile = ''
        }
    },

    created() {
        this.getCities(this.userLocation.tenantId)
    }
}
</script>

<style lang="scss" scoped>
@import '@/styles/global.scss';

.empty {
    width: 290px;
}
.Add-info {
    font-family: Roboto;
    font-size: 16px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.88;
    letter-spacing: normal;
    text-align: center;
    color: #9b9b9b;
}
.Upload-complete {
    font-size: 30px;
    font-weight: bold;
    color: #48545b;
    text-align: center;
}
.preview {
    width: 100%;
    max-width: 421px;
    max-height: 291px;
    background-color: #ffffff;
    border: solid 1px #cac6c6;
    padding: 37px 80px;
}
.icon {
    width: 27.1px;
    height: 27.4px;
    color: #9b9b9b;
}
.close-btn {
    width: 50px;
    height: 50px;
    background-color: #fbfbfb;
    position: absolute;
    margin-top: -25px;
    margin-left: -25px;
}
</style>

